  width: 93.75px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5px;
}

.clock .analog-clock {
  width: 75px;
  height: 75px;
  border: solid 4px #52ce52;
  border-radius: 50%;
  position: relative;
}

.clock .analog-clock .dial {
  position: absolute;
  left: 50%;
  width: 37.5px;
  height: 37.5px;
  transform-origin: bottom left;
}

.clock .analog-clock .dial.minutes {
  border-left: solid 1px #52ce52;
}

.clock .analog-clock .dial.hours {
